.main{
    background-color:rgb(214, 248, 255);
    width: 100%;
    display: flex;
    flex-direction: column;
}
.portfolio{
    font-family: "Source Sans Pro", Helvetica, sans-serif;
    font-weight: 300;
    font-size: 30pt;
    letter-spacing: -0.020em;
    color:rgb(40, 40, 126);
    text-align: center;
}
.sec1{
    margin-top: 70px;
    display: flex;
}
.sec1_left{
    width:40%;
    height: auto;
}
.sec1_right{
    background-color: rgb(240, 252, 255);
    margin: 15px;
    width:60%;
    height: auto;
    flex-direction: column;
}
.sec1_right_icon
{
    height: auto;
    text-align: center;  
}
.icon:hover{
    box-sizing: 2px 2px 4px grey;
}
.icon{
    padding: 5px;
    color: rgb(40, 40, 126);       
    margin:10px;
}
.sec1_right_bio{
    display: flex;
    height: auto;
}
.bio{
    width: 50%;
    text-align:justify;
    padding: 20px;
}
.bio_summary{
    font-size: 25px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    color: brown;
    font-weight: 500;
}
.bio_name{
    font-size: 15px;
    text-decoration: none;
}
.profile{
    width:50%;
    padding: 30px;
}
.profile_img{
    border-radius: 20%;
    width: 90%;
    height: auto;
}
.sec2{
    background-color: rgb(240, 252, 255);
    margin: 15px;
    display: flex;
    width: 60%;
    flex-direction: column;
    margin-top: 80px;
}
.sec2_highlights{
    text-align: center;
    font-size: 25px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    color: brown;
    font-weight: 500;
}
.sec2_image{
    display:flex;
    margin-bottom:40px;
}
.sec2_image1{
    width: 50%;
}
.sec2_image2{
    width: 50%;
}
.sec2_image1_react{
    width: 60%;
    margin: 20px;
}
.sec2_image2_java{
    width: 60%;
    margin:20px ;
}
.skillmeter{
    background-color: rgb(240, 252, 255);
    padding: 20px;
    margin: 20px;
}