@import url('https://fonts.googleapis.com/css2?family=Fira+Sans&family=Open+Sans&display=swap');

.resume{
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
}
.name{
    text-align: center;
    padding: 30px;
    font-family: "Source Sans Pro", Helvetica, sans-serif;
    color: brown;
    letter-spacing: -0.020em;
    font-size: 17px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    font-weight: 500;
}
.name_small{
    font-size: 11px;
}
.body{
    background-color: white;
    text-align: justify;
    display: flex;
    width: 90%;
    flex-direction: column;
    padding: 10px;
}
.skill{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}
.skill_tab{
    background-color: rgb(245, 250, 215);
    padding: 5px;                   
    font-size: auto;
}
.points{
    padding: 5px;
    font-size: 70%;
}
.skill_tabb{
    display: flex;
}
.skill_1{
    margin: 5px;
    width: 50%;
}
.skill_2{
    width: 50%;
    margin: 5px;
}
